import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import jiaoshiList from '@/views/pages/jiaoshi/list'
import jiaoshiDetail from '@/views/pages/jiaoshi/formModel'
import jiaoshiAdd from '@/views/pages/jiaoshi/formAdd'
import kechengleixingList from '@/views/pages/kechengleixing/list'
import kechengleixingDetail from '@/views/pages/kechengleixing/formModel'
import kechengleixingAdd from '@/views/pages/kechengleixing/formAdd'
import kechengxinxiList from '@/views/pages/kechengxinxi/list'
import kechengxinxiDetail from '@/views/pages/kechengxinxi/formModel'
import kechengxinxiAdd from '@/views/pages/kechengxinxi/formAdd'
import kaoqinxinxiList from '@/views/pages/kaoqinxinxi/list'
import kaoqinxinxiDetail from '@/views/pages/kaoqinxinxi/formModel'
import kaoqinxinxiAdd from '@/views/pages/kaoqinxinxi/formAdd'
import kechengpingjiaList from '@/views/pages/kechengpingjia/list'
import kechengpingjiaDetail from '@/views/pages/kechengpingjia/formModel'
import kechengpingjiaAdd from '@/views/pages/kechengpingjia/formAdd'
import xueshengchengjiList from '@/views/pages/xueshengchengji/list'
import xueshengchengjiDetail from '@/views/pages/xueshengchengji/formModel'
import xueshengchengjiAdd from '@/views/pages/xueshengchengji/formAdd'
import kechengdiaozhengtongzhiList from '@/views/pages/kechengdiaozhengtongzhi/list'
import kechengdiaozhengtongzhiDetail from '@/views/pages/kechengdiaozhengtongzhi/formModel'
import kechengdiaozhengtongzhiAdd from '@/views/pages/kechengdiaozhengtongzhi/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'jiaoshiList',
			component: jiaoshiList
		}, {
			path: 'jiaoshiDetail',
			component: jiaoshiDetail
		}, {
			path: 'jiaoshiAdd',
			component: jiaoshiAdd
		}
		, {
			path: 'kechengleixingList',
			component: kechengleixingList
		}, {
			path: 'kechengleixingDetail',
			component: kechengleixingDetail
		}, {
			path: 'kechengleixingAdd',
			component: kechengleixingAdd
		}
		, {
			path: 'kechengxinxiList',
			component: kechengxinxiList
		}, {
			path: 'kechengxinxiDetail',
			component: kechengxinxiDetail
		}, {
			path: 'kechengxinxiAdd',
			component: kechengxinxiAdd
		}
		, {
			path: 'kaoqinxinxiList',
			component: kaoqinxinxiList
		}, {
			path: 'kaoqinxinxiDetail',
			component: kaoqinxinxiDetail
		}, {
			path: 'kaoqinxinxiAdd',
			component: kaoqinxinxiAdd
		}
		, {
			path: 'kechengpingjiaList',
			component: kechengpingjiaList
		}, {
			path: 'kechengpingjiaDetail',
			component: kechengpingjiaDetail
		}, {
			path: 'kechengpingjiaAdd',
			component: kechengpingjiaAdd
		}
		, {
			path: 'xueshengchengjiList',
			component: xueshengchengjiList
		}, {
			path: 'xueshengchengjiDetail',
			component: xueshengchengjiDetail
		}, {
			path: 'xueshengchengjiAdd',
			component: xueshengchengjiAdd
		}
		, {
			path: 'kechengdiaozhengtongzhiList',
			component: kechengdiaozhengtongzhiList
		}, {
			path: 'kechengdiaozhengtongzhiDetail',
			component: kechengdiaozhengtongzhiDetail
		}, {
			path: 'kechengdiaozhengtongzhiAdd',
			component: kechengdiaozhengtongzhiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
